@import url('https://fonts.cdnfonts.com/css/gilroy-bold?styles=20879,20880');

@mixin main-font(
  $weight: 400,
  $size: 16px,
  $height: 1.2,
  $letter: 0.04em,
  $style: normal,
  $transform: none
) {
  font-family: 'Gilroy-Regular', sans-serif;
  font-weight: $weight;
  font-size: $size;
  line-height: $height;
  letter-spacing: $letter;
  font-style: $style;
  text-transform: $transform;
}
// ========  How to use:  ==============
// К примеру есть следующие стили из секции hero:
// font-family: 'Poppins';
// font-style: normal;
// font-weight: 400;
// font-size: 72px;
// line-height: 108px;

// Вот что мы пишем в scss:
// @include main-font(400, 72px, 108,
// =============================================
// Порядок задания свойств в скобочках -- font-weight, font-size, font-height, font-style (если ничего не передать, будет normal), text-transform (если ничего не передать, будет none)
